<template>
    <div class="ans-ctx" v-html="compiledMarkdown"></div>
</template>

<script>
import {marked} from 'marked';
import hljs from 'highlight.js';
import 'highlight.js/styles/default.css';
import { parseMarkdown } from '../utils'

export default {
    props: {
        answerCtx: {
            type: String,
            default: '...'
        }
    },
    data() {
        return {
            readText: '',
            markdown:'',
            // markdown: '# Hello, Markdown!\n\nThis is a _sample_ of **Markdown** text.\n\n```javascript\nconsole.log("Hello, World!");\n```\n',
        };
    },
    watch: {
        answerCtx(val){
            this.typeText(val)
        }
    },
    computed: {
        compiledMarkdown() {
            // 使用 marked 将 Markdown 转换为 HTML
            return this.markdown;
        },
    },
    mounted() {
        // 如果需要的话，可以在组件挂载后初始化高亮代码等操作
        // 这里以 highlight.js 为例，假设你已安装并引入了 highlight.js
        // 注意：这只是一个简化的示例，实际应用中可能需要更细致的配置
        const highlightCodeBlocks = () => {
            document.querySelectorAll('pre code').forEach((block) => {
                hljs.highlightBlock(block);
            });
        };
        this.$nextTick(()=>{
            highlightCodeBlocks();
        })
        this.typeText(this.answerCtx)
    },
    methods: {
        highlightCodeBlocks() {
            document.querySelectorAll('pre code').forEach((block) => {
                hljs.highlightBlock(block);
            });
        },
        typeText(text) {
            // this.processData.ansCtx = ''
            this.readText = ''
            let index = 0
            let interval = setInterval(() => {
                if (index < text.length) {
                    // this.processData.ansCtx += text.charAt(index)
                    this.readText += text.charAt(index)
                    this.markdown = parseMarkdown(this.readText, false)
                    index++
                } else {
                    clearInterval(interval)
                    // this.processData.status = 'DONE'
                    this.highlightCodeBlocks()
                }
            }, 30)
        },
    }
}
</script>

<style lang="less" scoped></style>